import React, {Component} from'react';
import { Grid,Cell} from 'react-mdl';

export default class Projects extends Component{
   

    render() {
        return(
    <div className="projecttabs">
    
    <Grid >
         <Cell col={4}>
                  
            <a href="https://github.com/abbiewachira/recipe-react-app" rel="noopener norefferrer" target="_blank">
                  <img
                  src="data:image/png;base64,"
                  alt="proj"
                  className="proj-img" />Recipe App
            </a>
                  
        </Cell>
                
        <Cell col={4}>
            <a href="https://github.com/abbiewachira/weather-app" rel="noopener norefferrer" target="_blank">
                <img
                    src="data:image/png;base64,"
                  alt="proj"
                  className="proj-img" /> Weather App
            </a>
        </Cell>

        <Cell col={4}>
        <a href="https://github.com/abbiewachira/memory-card-game-in-progress" rel="noopener norefferrer" target="_blank">
                <img
                    src="data:image/jpeg;base64,"
                  alt="proj"
                  className="proj-img" /> Memory Card Game
        </a>
                     
        </Cell>
    </Grid>
    </div>

        )
    }
}